import React, { useEffect } from 'react'
import "./profile.css"
import { ProfileCard, ProfileFeed, ProfileWidget } from '../../features/profile/index'
import { useParams } from 'react-router-dom'
import { useSelector } from 'react-redux'
import { selectAllUser } from '../../services/user/reducers/userSlice'
import EmptyQueryResult from '../../features/query/components/renderQueryPage/components/emptyQueryResult/EmptyQueryResult'
import PageHeader from '../../components/pageHeader/PageHeader'

const Profile = () => {
  const allUser = useSelector(selectAllUser)
  const {username} = useParams()

  const user = allUser?.find(user => user?.username === username)
  
  if(!user){
    return <div style={{padding: "0 1.8rem"}}>
      <PageHeader />
      <EmptyQueryResult emptySearchMessage={"Error! Couldn't find the page you're looking for."} />
    </div> 
  }

  return (
    <main className="profile-container">
      <div className="profile-layout">
        <ProfileCard />
        <ProfileFeed />
      </div>
      <ProfileWidget />
    </main>
  )
}

export default Profile
